import React from "react";
import ProjectItem from "./ProjectItem";
import Cocktail from "../../imgs/TheCocktailList.jpg";
import Memory from "../../imgs/MemoryGame.jpeg";
const ProjectList = () => {
  return (
    <div className="flex flex-col sm:pl-20 py-20 max-w-full h-full">
      <ProjectItem
        name={"The Cocktail List"}
        img={Cocktail}
        desc={
          "A simple app that delivers a list of searched cocktails through an API. First app made using React Hooks."
        }
        gitlink="https://github.com/anargiris/TheCocktailApp"
        demolink={"https://thecocktaillist.netlify.app/"}
        tech={["React", "Tailwind"]}
      />
      <ProjectItem
        name={"Memory Game(Desktop)"}
        img={Memory}
        desc={
          "A card-matching memory game with timer that uses a realtime leaderboard. Has some design experiments."
        }
        gitlink="https://github.com/anargiris/TheCocktailApp"
        demolink={"https://https://memorygamex.netlify.app/"}
        tech={["HTML", "CSS", "JavaScript", "Firebase"]}
      />
    </div>
  );
};

export default ProjectList;
