import React from "react";
import { ReactComponent as GithubIcon } from "../../icons/github-icon.svg";
import { ReactComponent as GotoIcon } from "../../icons/go-to-location.svg";

const ProjectItem = ({ name, desc, img, tech, gitlink, demolink }) => {
  return (
    <div className="w-full sm:h-full border-b-2 border-gray-900 font-Fira mb-10">
      <div className="flex flex-col  md:flex-row flex-1">
        <div className="w-60 mx-auto">
          <img
            src={img}
            alt=""
            className="w-full h-60 object-cover object-top opacity-90"
          />
        </div>
        <div className="flex flex-col flex-1">
          <h1 className="text-2xl sm:text-justify sm:p-0 py-2 text-center font-bold sm:ml-10 text-yellow-100 pointer-events-none">
            {name}
          </h1>
          <div className="sm:hidden flex gap-4 mx-auto">
            <a
              href={gitlink}
              target="_blank"
              rel="noreferrer"
              className="hover:scale-110 transition duration-150 transform"
            >
              <GithubIcon className="h-6 w-6" />
            </a>
            <a
              href={demolink}
              target="_blank"
              rel="noreferrer"
              className="hover:scale-110 transition duration-150 transform"
            >
              <GotoIcon className="h-6 w-6" />
            </a>
          </div>
          <p className=" sm:px-10 px-20 sm:py-10 sm:text-left text-center  text-yellow-50 pointer-events-none">
            {desc}
          </p>
        </div>
        <div className="hidden sm:block flex-1 text-center relative">
          <h1 className="text-yellow-100 underline text-lg pointer-events-none">
            Made with:
          </h1>
          <div className="flex justify-center pointer-events-none flex-wrap p-2">
            {tech.map((i) => {
              return i !== tech[tech.length - 1] ? i + ", " : i;
            })}
          </div>
          <div className="w-full flex flex-col absolute gap-2 bottom-0 justify-center">
            <h1 className="text-md font-bold text-yellow-50 underline pointer-events-none">
              Links:
            </h1>
            <div className="flex justify-center gap-5">
              <a
                href={gitlink}
                target="_blank"
                rel="noreferrer"
                className="hover:scale-110 transition duration-150 transform"
              >
                <GithubIcon className="h-6 w-6" />
              </a>
              <a
                href={demolink}
                target="_blank"
                rel="noreferrer"
                className="hover:scale-110 transition duration-150 transform"
              >
                <GotoIcon className="h-6 w-6" />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProjectItem;
