import React from "react";
import "./home.scss";

const MyShit = () => {
  const stars = [];

  for (let i = 0; i < 100; i++) {
    stars.push(
      <div className="circle-container" key={i}>
        <div className="circle"></div>
      </div>
    );
  }

  return (
    <div className="text-yellow-50 sm:p-20 pt-20 max-h-full">
      <div className="background">{stars}</div>
      <h1 className="sm:text-6xl text-5xl p-5 font-Garamond">
        Anargyros Stylidis
      </h1>
      <p className="text-2xl text-yellow-100 sm:ml-2 sm:p-10 p-8 font-Fira">
        Web developer located in Thessaloniki, Greece.
      </p>
      <p className="text-xl text-white sm:ml-2 px-10 font-Fira bg-black ">
        I like building single-page applications using React, Tailwind and{" "}
        NodeJS.
      </p>
      <p className=" text-xl text-white sm:ml-2 py-1 px-10 font-Fira">
        I've got great communication skills and perform better in a team.
      </p>
    </div>
  );
};

export default MyShit;
